import {
  HomeTwoTone,
  FaceTwoTone,
  CoffeeTwoTone
} from "@mui/icons-material";

const tabProps = (index) => {
  return {
    id: `sidebar-tab-${index}`,
    "aria-controls": `tabpanel-${index}`
  };
};

export const TabData = () => {
  const tabs = [
    {
      label: "",

      icon: <HomeTwoTone />,
      ...tabProps(0)
    },
    {
      label: " ",

      icon: <FaceTwoTone />,
      ...tabProps(1)
    },
   
    {
      label: "  ",

      icon: <CoffeeTwoTone />,
      ...tabProps(2)
    }
  ];
  return tabs;
};
